import axios from 'axios'
import qs from 'qs'
import { Message } from "element-ui";
import baseURL from './baseUrl'


// https://github.com/heuuLZP/vue-axios 
// 添加请求拦截器
axios.interceptors.request.use(config => {
    // loading
    return config
}, error => {
    return Promise.reject(error)
})

//添加响应拦截器
axios.interceptors.response.use(response => {
    return response
}, error => {
    return Promise.resolve(error.response)
})

function checkStatus (response) {
    // 如果http状态码正常，则直接返回数据
    if (response && (response.status === 200 || response.status === 304 || response.status === 400)) {
        return response
        // 如果不需要除了data之外的数据，可以直接 return response.data
    }
    // 异常状态下，把错误信息返回去
    return {
        status: -404,
        msg: '网络异常'
    }
}

function checkCode (res, successText) {
  // 如果code异常(这里已经包括网络错误，服务器错误，后端抛出的错误)，可以弹出一个错误提示，告诉用户
  if (res.status === -404) {
    Message({
        message:'数据获取失败，请联系管理人员',
        type:'error',
        showClose:true
    })
  }
  if (res.code == 0) {
    if(successText && successText =='sText'){
      Message({
        message:'操作成功',
        type:'success',
        showClose:true
      })
    }
  }
  return res
}

export default {
  post (url, data, successText) {
    return axios({
      method: 'post',
      baseURL: baseURL,
      url,
      data: qs.stringify(data),
      timeout: 10000,
      headers: {
        'X-Requested-With': 'XMLHttpRequest',
        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
      }
    }).then((response) => {
        return checkStatus(response)
    }).then((res) => {
        return checkCode(res, successText)
    })
  },
  get (url, params, successText) {
    return axios({
        method: 'get',
        baseURL: baseURL,
        url,
        params, // get 请求时带的参数
        timeout: 10000,
        headers: {
            'X-Requested-With': 'XMLHttpRequest'
        }
    }).then((response) => {
        return checkStatus(response)
    }).then(
        (res) => {
            return checkCode(res, successText)
        }
    )
  }
}


