import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Login from '@/components/login/login'
import Layout from '@/components/layout/layout'
import userRole from '@/components/user/userRole'

import weChatMenu from '@/components/weChatMenu/menu'

//error page

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [{
		path: '/',
		redirect: '/home'
	},{
		path: '/defaultLayout',
		component: Layout,
		children: [{
			path: '/home',
			component: HelloWorld,
		},{
			path: '/user/role',
			component: userRole
		},{
			path: '/weChatMenu/menu',
			component: weChatMenu,
		}]
	},{
		path: '/login',
		component: Login	
	},{
		path: '/error',
		component: r => require.ensure([], () => r(require('@/components/error/error')), 'error'),
		children: [{
				path: '/error/401',
				component: r => require.ensure([], () => r(require('@/components/error/401')), 'error'),
				meta:{
					title:'401'
				}
			},{
				path: '/error/403',
				component: r => require.ensure([], () => r(require('@/components/error/403')), 'error'),
				meta:{
					title:'403'
				}
			},{
				path: '/error/404',
				component: r => require.ensure([], () => r(require('@/components/error/404')), 'error'),
				meta:{
					title:'404'
				}
			},{
				path: '/error/500',
				component: r => require.ensure([], () => r(require('@/components/error/500')), 'error'),
				meta:{
					title:'500'
				}
			}]
	}]
})

//过滤 404
router.beforeEach((to, from, next) => {
	if(sessionStorage.getItem('userName')){
		if(to.matched.length){
			next()
		}else{
			router.replace('/error/404')
		}
	}else{
		if (to.path === '/login') {
            next()
        } else {
            router.replace('/login')
        }
	}
})

export default router


