<template>
    <div class="v-sider">
        <el-menu 
            :default-active="onRoutes"
            class="el-menu-vertical-demo v-sider"
            :router=true
            @select=clickMenu
            :collapse="collapse">
    
            <template v-for="(item,index) in leftMenus">
                <template v-if="item.child">
                    <el-submenu :index="item.level" :route="{path: item.path}" :key="item.index">
                        <template slot="title">
                            <i :class="item.icon"></i><span slot="title">{{ item.title }}</span>
                        </template>
                        <el-menu-item v-for="(subItem,i) in item.child" :key="i" :index="subItem.level" :route="{path: subItem.path}">
                            {{ subItem.title }}
                        </el-menu-item>
                    </el-submenu>
                </template>
                <template v-else>
                    <el-menu-item :index="item.level" :route="{path: item.path}" :key="item.index">
                        <i :class="item.icon"></i><span slot="title">{{ item.title }}</span>
                    </el-menu-item>
                </template>
            </template>
        </el-menu>
    </div>
    
</template>

<script>
export default {
    data(){
        return {
            onRoutes: sessionStorage.getItem('defultActive') ? sessionStorage.getItem('defultActive') : '1' ,
            collapse: false,
            leftMenus: [
                {
                    title:'首页',
                    path:'/home',
                    level:'1',
                    icon:'el-icon-menu',
                },{
                    title:'客户管理',
                    level:'2',
                    icon:'el-icon-service',
                    child:[{
                        title:'用户列表',
                        path:'/user/list',
                        level:'2-1',
                    },{
                        title:'微信菜单管理',
                        path:'/weChatMenu/menu',
                        level:'2-2',
                    },{
                        title:'用户角色管理',
                        path:'/user/role',
                        level:'2-3',
                    }]
                },

            ]
        }
    },
    methods: {
        clickMenu(index,indexPath){
            sessionStorage.setItem('defultActive',index)
            this.onRoutes = index
        },
    },
    computed:{
        isCollapse(){
            return this.$store.state.isCollapse
        }


    },
    watch: {
        isCollapse(data){
            this.collapse = data
        }
    },
    

}
</script>

<style>
    .v-sider {
        height: 100%;
    }
    .el-menu-vertical-demo:not(.el-menu--collapse) {
        width: 210px;
        min-height: 400px;
    }
</style>



