<template>
    <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect" >
        <el-menu-item index="1" v-if="activeCollapse"><i class="el-icon-d-arrow-right"></i></el-menu-item>
        <el-menu-item index="1" v-else><i class="el-icon-d-arrow-left"></i></el-menu-item>

        <el-submenu index="2" class="right">
            <template slot="title">admin</template>
            <el-menu-item index="2-1">退出登录</el-menu-item>
        </el-submenu>
    </el-menu>
</template>

<script>
export default {
    data() {
        return {
            activeIndex: '1',
        }
    },
    watch: {
    },
    methods: {
        handleSelect(key, keyPath) {
            switch(key){
                case '1':
                    this.$store.dispatch('changeCollapse');
                    break
                case '2-1':
                    sessionStorage.removeItem('userName');
                    this.$router.push({path:'/login'});
                    break
                default :
                    return 
            }
        }
    },
    computed: {
        activeCollapse(){
            return this.$store.state.isCollapse
        }
    }
}
</script>


<style> 
    .right{
        float: right!important;
    }

</style>


