<template>
    <div class="sys-login-bg">
        <div class="sys-login">
            <el-form ref="loginForm" :model="loginForm" label-width="80px" :rules="formRules">
                <el-form-item label="用户名" prop="name">
                    <el-input v-model="loginForm.name" placeholder="admin"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="pwd">
                    <el-input v-model="loginForm.pwd" type="password" placeholder="admin"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="clickLogin('loginForm')">登 录</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
    
</template>

<script>

const user_reg = (rule, value, callback) => {
    let reg = /^admin$/;
    if(!reg.test(value) && !value == ''){
        return callback(new Error('输入值默认为：admin'));
    }else{
        callback()
    }
}

export default {
    data(){
        return {
            loginForm: {
                name: '',
                pwd: '',
            },

            formRules: {
                name: [
                    { required: true, message: '请输入默认用户名登录', trigger: 'blur' },
                    { validator: user_reg, trigger:'blur' }
                ],
                pwd: [
                    { required: true, message: '请输入默认用户名密码', trigger: 'blur' },
                    { validator: user_reg, trigger:'blur' }
                ],
            }


        }
    },
    methods: {
        clickLogin(formName) {
            this.$refs[formName].validate((valid)=>{
				if(valid){
                    setTimeout( ()=>{
                        sessionStorage.setItem('userName','admin')
                        sessionStorage.setItem('defultActive','1')
                        this.$router.push({path:'/home'})
                    },500)
					
				}else {
					console.log('err submit')
					return false;
				}
					
			})
        }
    }
}
</script>
<style scoped>
.sys-login-bg {
    height: 100%;
    width: 100%;
    background-color: #ccc;
}
.sys-login {
    margin: 0 auto;
    width: 400px;
    position: relative;
    top: 30%;
    background: #fff;
    padding: 40px;
    border-radius: 10px;
}
</style>


