<template>
    <div>
        <el-row :gutter="20">
            <el-col :span="6" v-for="(item, index) in menu" :key="index" >
                <el-card class="box-card" >
                    <div slot="header" class="clearfix">
                        <span>{{item.name}}</span>
                        <el-button style="float: right; padding: 3px 0; margin-left: 10px" type="text" @click="addMenu(item,index)">添加子菜单</el-button>
                        <el-button style="float: right; padding: 3px 0"  type="text">编辑</el-button>
                        <el-button style="float: right; padding: 3px 0"  type="text" @click="removeMenu(item,index)">删除</el-button>
                    </div>
                    
                    <div v-for="(o,i) in item.subButton" :key="i" class="text item el-c-item">
                            <div>菜单名称：{{ o.name }}</div>
                            <div>key：{{o.key}}</div>
                            <div>url：{{o.url}}</div>
                            <div>type：{{o.type}}</div>

                            <el-button type="text" @click="editItem(o, index, i)">编辑</el-button>
                            <el-button type="text" @click="removeItem(index, i) ">删除</el-button>
                        
                    </div>

                </el-card>
            </el-col>
            <el-col :span="6">
                <el-button @click="saveMenu()">添加 +</el-button>
                <el-button type="primary" @click="submitMenu()">保存菜单</el-button>
            </el-col>
        </el-row>
        
        <el-dialog :title="dialogTitle" :visible.sync="dialogForm" :close-on-click-modal=false :show-close=false>
            <el-form v-model="menuForm" label-width="80px" >
                <el-form-item label="菜单名称">
                    <el-input v-model="menuForm.name"></el-input>
                </el-form-item>
                <el-form-item label="类型">
                    <el-select v-model="menuForm.type">
                        <el-option label="点击" value="click"></el-option>
                        <el-option label="跳转" value="view"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="跳转链接" v-if="menuForm.type == 'view'">
                    <el-input v-model="menuForm.url"></el-input>
                </el-form-item>
                <el-form-item label="key" v-else>
                        <el-input v-model="menuForm.key"></el-input>
                    </el-form-item>
                <el-form-item>
                    <el-button type="primary" size="small" @click="saveForm">确定</el-button>
                    <el-button type="primary" size="small" @click="cancel">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

    </div>
</template>

<script>
import { getTree, updateTree } from '@/config/getData'
export default {
    data() {
        return {
            dialogTitle:'添加',
            dialogForm: false,
            menuForm: {
                name: '',
                type: '',
                key: '',
                url: '',
                subButton : [],
            },
            menu: '',
            menuIndex:'',
            itemIndex:'',
        }
    },
    mounted() {
        //获取树结构
        getTree().then(res =>{
            this.menu = res.data.data.button
        })
    },
    methods: {
        //添加父菜单
        saveMenu(){
            if(this.menu.length >= 3){
                this.$message.error('添加不能超过3个')
                return 
            }else{
                this.dialogForm = true;
                this.menuIndex = '';
                this.itemIndex = '';
            }
            
        },
        
        //添加子菜单
        addMenu(data,index){
            console.log(data)
            if(data.subButton.length >=5){
                this.$message.error('添加不能超过5个')
                return 
            }else{
                this.menuIndex = index ;
                this.itemIndex = ''
                this.dialogForm = true ;
                this.menuForm = { name: '',type: '',key: '',url: '', subButton :[] }
            } 
        },

        //
        removeMenu(item,index){
            this.menu.splice(index,1)
        },

        //编辑
        editItem(data,index,itemIndex){
            this.menuIndex = index;
            this.itemIndex = itemIndex;
            this.dialogForm = true;
            this.menuForm = Object.assign({},data)
        },
        
        //删除
        removeItem(index,i){
            console.log(this.menu[index])
            this.menu[index].subButton.splice(i,1)
        },
        
        saveForm(){
            if(this.itemIndex != ''){
                //编辑子菜单
                this.menu[this.menuIndex].subButton[this.itemIndex] = this.menuForm
            } else if(this.itemIndex ==='' && this.menuIndex === ''){
                //增加父菜单
                this.menu.push(this.menuForm)
            } else {
                //增加子菜单
                this.menu[this.menuIndex].subButton.push(this.menuForm);
            }
            this.dialogForm = false;
        },

        submitMenu(){
            updateTree({
                button:JSON.stringify(this.menu)
            }).then(res =>{
                this.$message({
                    type:'success',
                    message:'保存成功'
                })
            })
        },

        cancel(){
            this.dialogForm = false;
        }
    }

}
</script>

<style>
    .el-c-item{
        font-size: 14px;
        padding: 3px;
    }
    .el-c-btn{
        margin: 0px;
        padding: 0px 5px 0px 0px;
        float: right;
    }
</style>



